* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: "PT Sans", serif;
    background-image: url(/static/media/main.ea15f621.jpg);
    background-size: cover;
}

/* header */
.header {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.4);
}

/* Title container */
.title {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Logo */
.logo img {
    width: 50px;
}

/* Title and tagline */
.titlename {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-weight: 700;
    font-style: italic;
    padding-left: 20px;
}

/* Title styling */
.titlename h1 {
    font-size: 40px;
    color: white;
}

/* Tagline styling */
.tagline {
    font-size: 18px;
    color: white;
    font-weight: 400;
    padding-top: 10px;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
    .header {
        background-color: rgba(0, 0, 0, 0.4);
    }
    .logo img {
        width: 40px;
    }

    .titlename h1 {
        font-size: 20px;
    }

    .tagline {
        font-size: 12px;
        padding-left: 0;
    }
}

.content {
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
  width: 100%;
}

.content .row {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.content .col {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content input, .content button {
  border-radius: 4px;
  padding: 8px;
  margin: 10px 0;
  width: 250px;
  box-sizing: border-box;
}

.content button {
  background-color: #ecd69f;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 15px;
}

.content input {
  border: 1px solid #ccc;
}

.content input:focus, .content button:focus {
  outline: none;
}

.recipe {
  list-style: none;
  padding: 10px;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding-bottom: 200px;
}

.recipe li {
  margin: 10px;
  padding: 15px;
  background-color: #343a40;
  border-radius: 8px;
  text-align: center;
  transition: background-color 0.3s;
  width: 150px;
}

.recipe li:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.recipe li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  display: block;
}

.recipe li a:hover {
  color: #007bff;
}

.error-box {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 5px;
  font-size: 16px;
}

.error p {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 5px;
  font-size: 16px;
}

.loading-animation {
  font-size: 16px;
  color: #555;
  text-align: center;
  margin-top: 20px;
}

.loading-animation:after {
  content: '...';
  animation: dots 1.5s steps(3, end) infinite;
}

@keyframes dots {
  0% {
    content: '';
  }
  33% {
    content: '.';
  }
  66% {
    content: '..';
  }
  100% {
    content: '...';
  }
}

@media (max-width: 768px) {
  html{
    overflow: hidden;
  }
  .content{
    height: 100%;
  }
  .content .col {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    width: 90%;
  }

  .content input {
    margin-bottom: 10px;
    padding: 15px;
    font-size: 18px;
    width: 90%;
  }

  .content button {
    padding: 15px;
    font-size: 16px;
    width: 70%;
    margin-bottom: 40px;
  }

  .recipe li a {
    font-size: 18px;
    padding-right: 18px;
  }

  .recipe {
    width: 100%;
    height: 100%;
  }
}

